<template><section><html><head></head><body><h1>CzmlDataSource</h1>
<p>The <code v-pre="">vc-datasource-czml</code> component is used to load a dataSource which processes <a href="https://github.com/AnalyticalGraphicsInc/czml-writer/wiki/CZML--guide">CZML</a>.</p>
<h2>Example</h2>
<h3>Load a CzmlDataSource with <code v-pre="">vc-datasource-czml</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer :should-animate="true" @ready="ready">
        <vc-datasource-czml czml="./statics/SampleData/simple.czml" @ready="subReady" :show="show"></vc-datasource-czml>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>Code</h4>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">:should-animate</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-datasource-czml</span> <span class="hljs-attr">czml</span>=<span class="hljs-string">"./statics/SampleData/simple.czml"</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"subReady"</span> <span class="hljs-attr">:show</span>=<span class="hljs-string">"show"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-datasource-czml</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-title function_">data</span>(<span class="hljs-params"></span>) {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">show</span>: <span class="hljs-literal">true</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      <span class="hljs-title function_">ready</span>(<span class="hljs-params">cesiumInstance</span>) {},
      <span class="hljs-title function_">subReady</span>(<span class="hljs-params">cesiumInstance</span>) {
        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer } = cesiumInstance
        viewer.<span class="hljs-property">camera</span>.<span class="hljs-title function_">flyHome</span>(<span class="hljs-number">0</span>)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
